import React from "react";
import { Component } from "react";

class Todolist extends Component {

    state = {
        text : ""
    }

    handlechange = (event) =>{
        this.setState=( { text : event.target.value 
     });
    };

    render (){    
    return(
     <input name= "text"
            value={this.state.text}
            placeholder="todooo..."
            onChange= {this.handlechange}/>
    )
} }

export default Todolist 